<template>
<div id="sidebar-nav" v-if="context.hasOwnProperty('header')">
  <div id="sidebar" :class="{ active: active }">
    <ul>
      <li v-for="section in context.header.side_menu">
        <h3 class="title">{{ section.title }}</h3>
        <ul>
          <li v-for="link in section.links">
            <a v-if="isExternalLink(link.link)" :href="link.link">
              {{ link.title }}
            </a>
            <router-link :to="link.link" v-else v-on:click.native="disableSidebar">
              {{ link.title }}
            </router-link>
          </li>
        </ul>
      </li>
      <li>
        <h3 class="title">Social</h3>
        <ul>
          <div class="social">
            <img src="../assets/icons/facebook.png">
            <img src="../assets/icons/twitter.png">
            <img src="../assets/icons/profile.png">
          </div>
        </ul>
      </li>

    </ul>
  </div>
  <slot id="push-content"></slot>
  <div id="pusher" :class="{ active: active }" v-on:click="disableSidebar">
  </div>
</div>
</template>
<script>
export default {
  name: 'SidebarNav',
  props: [
    'active',
    'context'
  ],
  methods: {
    disableSidebar () {
      if (this.active) {
        this.$emit('toggleSidebar')
      }
    },
    isExternalLink (url) {
      if (/^(f|ht)tps?:\/\//i.test(url)) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style scoped>
#sidebar-nav {
  height: 100%;
  min-height: 100%;
  /* Disable because scrollto is not working for now
  overflow-x: hidden;
  */
  position: relative;
  background: #131313;
  width: 100%;
  display: table;
}

#sidebar {
  width: 300px;
  background: #1c4de6;
  height: 100%;
  position: fixed;
  top: 85px;
  left: 0;
  bottom: 0;
  overflow-y: auto;
  z-index: 999;
  transform: translate3d(-300px, 0, 0);
  transform-origin: 100% 50%;
  transition: transform 0.5s;
}
@media screen and (max-width: 980px) {
  #sidebar {
    top: 106px;
  }
}

@media screen and (max-width: 960px) {
  #sidebar {
    top: 119px;
  }
}
@media screen and (max-width: 479px) {
  #sidebar {
    top: 116px;
  }
}
@media screen and (max-width: 297px) {
  #sidebar {
    top: 136px;
  }
}

#sidebar.active {
  transform: translate3d(0, 0, 0);
  box-shadow: 4px 4px 4px rgba(0,0,0,0.3);
}

#pusher {
  background: black;
  transition: transform 0.5s;
  transform-style: preserve-3d;
  height: 100%;
  width: 100%;
  display: table;
  table-layout: fixed;
}

#pusher.active {
  transform: translate3d(300px, 0, 0);
  opacity: 0.5;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
}

#pusher.active #header,
#pusher.active #content,
#pusher.active #footer {
  pointer-events: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}
.title {
  font-size: 20px;
  text-transform: uppercase;
  padding-left: 50px;
}

ul li li a {
  padding: 10px 0 10px 100px;
}
a {
  color: white;
  display: block;
}

a:hover {
  text-decoration: none;
  background: #20a4f3;
}

.social {
      padding: 10px 0 10px 100px;
}
</style>



